.app.dark {
  background-color: #111;
  color: #9c9c9c;
}

.app.dark .table {
  background-color: #111;
}

.app.dark .table .tableCell {
  color: gray;
}

.app.dark .navbar {
  color: #999;
  border-color: #333;
}

.app.dark .navbar .search {
  border-color: gray;
}

.app.dark .sidebar {
  background-color: #111;
  border-color: #333;
}

.app.dark .sidebar .link {
  border-bottom-color: #333;
}

.app.dark .sidebar .lk {
  color: #999;
}

.app.dark .sidebar ul li:not(.head):hover {
  background-color: #333;
}

.app.dark .sidebar ul li:not(.head) .icon {
  color: #999;
}

.app.dark .datatable .datagrid {
  color: gray;
  border: none;
}

.app.dark .datatable .datagrid .viewButton,
.app.dark .datatable .datagrid .deleteButton,
.app.dark .datatable .datagrid .cellWithStatus {
  color: gray;
  border: none;
}

.app.dark input {
  background-color: transparent;
}
